/**
 * @jest-environment jsdom
 */
require('@testing-library/jest-dom');
const React = require('react');
const { render, screen } = require('@testing-library/react');

// ======================================================================
// BAGIAN 1: PERSIAPAN LINGKUNGAN
// ======================================================================
// Kita tetap perlu mock 'next/image' karena ini adalah dependensi dari Profile.
jest.mock('next/image', () => {
  // eslint-disable-next-line @next/next/no-img-element
  return (props) => <img {...props} />;
});


// ======================================================================
// BAGIAN 2: MENGAMBIL KODE MAHASISWA
// ======================================================================
if (!process.env.SUBMISSION_PATH) {
  throw new Error('SUBMISSION_PATH environment variable not set.');
}
const submission = require(process.env.SUBMISSION_PATH);
const Gallery = submission.Gallery || submission.default || submission;


// ======================================================================
// BAGIAN 3: CHECKLIST PENILAIAN YANG SUDAH DIPERBAIKI
// ======================================================================
describe('Praktikum: Komponen Gallery', () => {

  // Render komponen sebelum setiap tes
  beforeEach(() => {
    render(<Gallery />);
  });

  test('Kriteria 1: Harus me-render sebuah <div> sebagai pembungkus utama', () => {
    // Kita cari container utamanya.
    const container = document.querySelector('body > div > div'); // div di dalam body
    expect(container).toBeInTheDocument();
  });

  test('Kriteria 2: Harus me-render TEPAT 3 elemen gambar di dalamnya', () => {
    // Tes yang paling penting: hitung jumlah gambar yang muncul.
    const images = screen.getAllByRole('img');
    expect(images).toHaveLength(3);
  });

  test('Kriteria 3: Komponen "Gallery" harus diexport dengan benar', () => {
      expect(Gallery).toBeDefined();
  });

});
